import React, { useState } from 'react';
import {RiLoginBoxFill} from 'react-icons/ri'
import {HiDownload} from 'react-icons/hi'
import api from '../services/api';
import xlsx from "json-as-xlsx"

import styles from './styles.module.css';

type Data = {
    id: string,
    message: string,
    name: string
}

type Table = {
    table: any, 
    settings: object
}
type Error = {
    response: {
        data:{
            message: string
        }
    }
}
const Home: React.FC = () => {
    const [authenticated, setAuthenticated] = useState<boolean>(false)
    const [loading, setLoading] = useState<boolean>(false)
    const [nameProject, setNameProject] = useState<string>('')
    const [password, setPassword] = useState<string>('')
    const [data, setData] = useState<Data>()

    async function onDownload() {
        setLoading(true)
        try {
            const response = await api.get(`/download/${data?.id}`)
            console.log(response.data)
            const jsonToExcel = response.data 
            xlsx(jsonToExcel?.table[0], jsonToExcel?.settings)
        }
        catch(error){
            const {response:{data:{message}}} = error as Error
            alert(message)
        }
        finally{
            setLoading(false)
        }
    }

    async function onLogin(){
        if(nameProject ===''|| password===''){
            alert('Preencha os campos!!!')
            return
        }
        setLoading(true)
        try {
            const response = await api.post('/auth/login',{
                name: nameProject, 
                password:password
            })
            
            setData(response.data)
            setAuthenticated(true)
            alert(`Bem vindo ${response.data?.name}`)
        }
        catch(error){
            const {response:{data:{message}}} = error as Error
            alert(message)
        }
        finally{
            setLoading(false)
        }

    }
    return (
        <div className={styles.container}>

            <h1 className={styles.title}>Delta Download</h1>

            <div className={styles.containerLogin}>

            <span className={styles.label}>Nome do Projeto</span>
            <input className={styles.input} onChange={({currentTarget})=> setNameProject(currentTarget.value)} type="text" placeholder='Nome do projeto'/>

            <span className={styles.label}>Senha</span>
            <input className={styles.input} onChange={({currentTarget})=> setPassword(currentTarget.value)} type="password" name="pass" id="pass" placeholder='Senha' />

            {
                authenticated?

                    <button onClick={onDownload} className={styles.buttonDownload}>
                        {
                            loading?
                                <span>...</span>
                            :   <HiDownload size={40}/>
                        }
                    </button>
                :
                    <button onClick={onLogin} className={styles.buttonLogin}>
                        {
                            loading?
                                <span>...</span>
                            :   <RiLoginBoxFill size={40}/>
                        }
                    </button>
            }

            </div>
        </div>
    );
}

export default Home;